// 
export default {
  /**
   *  ()
   */
  isProdEnv() {
    return process.env.VUE_APP_ENV === 'production'
  },
  /**
   *   production
   */
  isRunLocalServer() {
    return process.env.NODE_ENV === 'development'
  },
  /**
   * @returns true
   */
  isProdDistribution() {
    return !this.isRunLocalServer() && this.isProdEnv()
  },
  /**
   * @returns true
   */
  isDevDistribution() {
    return !this.isRunLocalServer() && !this.isProdEnv()
  },
}
